.projects {
  background-image: url(../Assets/background2.jpg);
  background-size: cover;
}

.projectsLabel {
  text-align: center;
  color: white;
  font-size: 3.5rem;
  margin: 10px auto;
}

.projectsDiv {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 100%;
}

.projectCard {
  width: 100%;
  padding: 10px;
  margin: 5px 0 35px 0;
  text-align: center;
  border: 1px solid #2d03a5;
}

.projectCard:hover .projectPic {
  opacity: 0.3;
}

.description {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 5rem;
  opacity: 0;
  padding: 10px;
  background-color: #2d03a5;
  color: white;
  margin: 0;
  font-size: 12px;
}

.projectCard:hover .description {
  opacity: 0.8;
}

.projectPic {
  width: 100%;
}

.projBtn {
  color: white;
}
