.aboutMe {
  color: black;
  background-color: white;
  width: 100%;
  margin: 0;
  padding: 20px;
}

.aboutLabel {
  text-align: center;
  font-size: 3.5rem;
  margin: 35px auto 5px auto;
  width: 100%;
}

.aboutContent {
  width: 100%;
  padding: 20px;
  text-align: center;
}

.aboutContent > p {
  width: 50%;
  margin: 0 auto;
}

.bioText{
  margin:0 auto;
  font-size: 1.1rem;
}

.profilePic {
  border: 1px solid #2d03a5;
  box-shadow: 5px 10px 7px gray;
  height: 150px;
  margin-bottom: 15px;
}

.qualitiesDiv {
  display: flex;
  justify-content: space-center;
  flex-wrap: wrap;
}

.qualities, .skills {
  padding: 10px;
  margin: 5px 0 35px 0;
  text-align: center;
  border: 2px solid #2d03a5;
}

.cardTitle {
  background-color: #2d03a5;
  color: white;
  padding: 5px;
}

.qualitiesPic {
  width: 100%;
  object-fit: contain;
  height: 250px;
}

.skillSetDiv {
  display: flex;
  flex-wrap: wrap;
  background-color: rgb(247, 245, 245);
  padding: 15px;
  margin: 10px auto;
  border: 2px solid #2d03a5;
}

.skillSetLabel {
  font-size: 3rem;
}

.skillsSet {
  margin:15px 0;
}

.skillsSet:hover{
  transform: scale(1.1);
}

.skillsSet > h6 {
  align-self: center;
  text-align: center;
  width: 120px;
}

.progress {
  height: 30px;
  width: 100%;
  margin: 10px;
  background-color: #2d03a5;
  color: black;
}
