.mainDiv {
  flex-grow: 1;
  padding: 60px 0;
  background-color: #2d03a5;
}

.bannerContainer {
  display: flex;
  flex-wrap: wrap;
  height: 650px;
}

.imgDiv {
  background-color: #2d03a5;
}

.imgDiv > img {
  padding-top: 70px;
  width: 100%;
}

.bannerText {
  background-color: #2d03a5;
  align-self: center;
  padding-left: 25px;
}

.bannerIntro {
  color: white;
  text-align: center;
  width: 100%;
  font-size: 3rem;
  line-height: 6rem;
}
